import * as React from 'react';
import { css,StyleSheet,minify } from 'aphrodite';

minify(false);

class Navbar extends React.Component {
  public render() {
    return (
        <div id="nav_bar_container" className={css(styles.navbar_container)} >
            <div id="nav_bar" className={css(styles.navbar)}>

                <a id="nav_home" href="/" className={css(styles.navbarItem)} >
                    iPTMnet
                </a>

                <div id="seperator" className={css(styles.seperator)} ></div>
                
                <a id="nav_browse" href="/" className={css(styles.navbarItem)} >
                    Browse
                </a>

                <div id="seperator" className={css(styles.seperator)} ></div>          
                
                <a id="nav_stats" href="/" className={css(styles.navbarItem)} >
                    Stats
                </a>
                
                <div id="seperator" className={css(styles.seperator)} ></div>
                
                <a id="nav_api" href="/" className={css(styles.navbarItem)} >
                    API
                </a>
                
                <div id="seperator" className={css(styles.seperator)} ></div>

                <a id="nav_help" href="/" className={css(styles.navbarItem)} >
                    Help
                </a>

                <div id="seperator" className={css(styles.seperator)} ></div>

                <a id="nav_license" href="/" className={css(styles.navbarItem)} >
                    License
                </a>
                
                <div id="seperator" className={css(styles.seperator)} ></div>

                <a id="nav_citation" href="/" className={css(styles.navbarItem)} >
                    Citation
                </a>

                <div id="seperator" className={css(styles.seperator)} ></div>  

                <a id="nav_about" href="/" className={css(styles.navbarItem)} >
                    About
                </a>

                <div id="seperator" className={css(styles.seperator)} ></div>

                <div id="search container" >
                    
                </div>
            </div>
        </div>
    );
  }
}

const styles = StyleSheet.create({
  navbar: {
    height: 40,
    backgroundColor: "#329CDA",
    display: "flex",
    flexDirection: "row",
    alignItems: "center",
    fontSize: "1em"
  },

  navbar_container: {
    position: "sticky",
    top: 0,
    boxShadow: "0,4,8,#83838354"
  },

  navbarItem : {
    color: "#FFFFFF",
    paddingLeft: 40,
    paddingRight: 40,
    lineHeight: '40px',
    fontSize: "1em",
    textDecoration: "none",
    ':hover': {
        cursor: "pointer",
        backgroundColor: "#00000017"
    }
  },

  seperator : {
    height: "50%",
    width : "1px",
    backgroundColor: "#FFFFFF"
  }

});

export default Navbar;



